import '../../less/common.less';
import '../../less/footer.less';
import './index.less';

import $ from 'jquery';
import '../../js/nav';
import browser from '../../js/browser';
import '../../js/switch-lang';
import '../../js/notify';

function initDom(){

    $('.navigation .nav-opt').click(function () {
        // var type = $(this).data('type');
        $('html, body').animate({
            scrollTop: $($.attr(this, 'href')).offset().top
        }, 500);
    });

    if (browser.isMobile() === true) {
        $('#email').attr('placeholder', 'Please enter the email');
    }

    //轮播
    var modify, translate, scale, zIndex, i;
    new Swiper('.swiper-container', {
        watchSlidesProgress: true,
        slidesPerView: 'auto',
        centeredSlides: true,
        loop: true,
        loopedSlides: 7,
        autoplay: true,
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        pagination: {
            el: '.swiper-pagination',
            //clickable :true,
        },
        on: {
            progress: function(progress) {
                for (i = 0; i < this.slides.length; i++) {
                    var slide = this.slides.eq(i);
                    var slideProgress = this.slides[i].progress;
                    modify = 1;
                    if (Math.abs(slideProgress) > 1) {
                        modify = (Math.abs(slideProgress) - 1) * 0.3 + 1;
                    }
                    translate = slideProgress * modify * 260 + 'px';
                    scale = 1 - Math.abs(slideProgress) / 5;
                    zIndex = 999 - Math.abs(Math.round(10 * slideProgress));
                    slide.transform('translateX(' + translate + ') scale(' + scale + ')');
                    slide.css('zIndex', zIndex);
                    slide.css('opacity', 1);
                    if (Math.abs(slideProgress) > 3) {
                        slide.css('opacity', 0);
                    }
                }
            },
            setTransition: function(transition) {
                for (var i = 0; i < this.slides.length; i++) {
                    var slide = this.slides.eq(i);
                    slide.transition(transition);
                }

            }
        }
    });



    const LOCAL_PREFIX = 'EVENT_';

    $('.join-us-content .item-link').click(function (e) {
        let $this = $(this);
        if (window.localStorage.getItem(LOCAL_PREFIX)) {
            window.open($this.data('link'));
        } else {
            $('html, body').animate({
                scrollTop: $('#joinUs').position().top
            }, 'normal');
            $('#email').focus();
        }
    });

    let regEmail = /^.+@.+\..+/;
    $('#submit').click(function (event) {
        var $this = $(this);
        var $email = $('#email');
        // var name = $('#name').val().trim();
        // if (!name) {
        //     $('#name').notify('Please enter the username', 'warn');
        // }
        var email = $email.val().trim();
        if (!email) {
            $email.notify('Please enter the email', 'warn');
            return;
        }
        if (regEmail.test(email) === false) {
            $email.notify('Invalid email format', 'warn');
            return;
        }
        var value = window.localStorage.getItem(LOCAL_PREFIX + email);
        if (value) {
            $email.notify('You have already submitted', 'warn');
            return;
        }
        $this.attr('disabled', true);
        var name = browser.isMobile() === true ? 'h5-page' : 'pc-page';
        $.ajax({
            type: "POST",
            url: "https://tweebaa.com/api/v1/uas/signnewsletter",
            data: {
                n: name,
                e: email
            },
            dataType: "json",
            success: function(rep){
                $this.attr('disabled', false);
                if (rep.success === true) {
                    $this.notify('Your submission has been received', 'success');
                    $email.val('');
                    window.localStorage.setItem(LOCAL_PREFIX, true);
                    window.localStorage.setItem(LOCAL_PREFIX + email, true);
                } else {
                    if (rep.message === '无效电子邮件地址') {
                        $this.notify('Invalid email format', {
                            className: 'error',
                            globalPosition: 'top center'
                        });
                    } else {
                        $this.notify(rep.message, {
                            className: 'error',
                            globalPosition: 'top center'
                        });
                    }
                }
            }
        });

    });

}

initDom();




